import React from 'react';
import countapi from 'countapi-js';

var value = 0;
countapi.visits().then((result) => {
    value = result.value;
});

function Contact(){
    return(
        <div>
            <center>
                This site is under development, Please visit later.
                <h6>Visitor count: {value}</h6>
            </center>
        </div>
    );
}

export default Contact;